import { SET_REWARD } from '../../resources/constants';

const initial_state = {
  reward_per_token: 0,
};

interface Action {
  value: string;
  type: string;
}

export const set_reward_reducer = (state = initial_state, action: Action) => {
  switch (action.type) {
    case SET_REWARD:
      return {
        ...state,
        reward_per_token: action.value,
      };
    default:
      return state;
  }
};
