import * as action from '../../resources/constants';
import * as api from '../../api/api';

export const set_shows = (value: string) => ({
  type: action.SET_LANGUAGE,
  value,
});

export const get_contract = (value: any) => ({ type: action.GET_CONTRACT, value });
export const set_supply = (value: any) => ({ type: action.SET_SUPPLY, value });
export const set_reward = (value: any) => ({ type: action.SET_REWARD, value });
export const get_address = (value: any) => ({ type: action.GET_ADDRESS, value });
export const post_contract_data = (value: any) => ({ type: action.POST_CONTRACT, value });
export const get_rate = (value: any) => ({ type: action.GET_RATE, value });
export const get_health = (value: any) => ({ type: action.GET_HEALTH, value });
export const get_transactions = (value: any) => ({ type: action.GET_TRANSACTIONS, value });
export const get_transactions_transactions_specific = (value: any) => ({
  type: action.GET_TRANSACTIONS_SPECIFIC,
  value,
});
export const put_transactions = (value: any) => ({ type: action.PUT_TRANSACTIONS, value });

export const get_contract_address_thunk =
  (address: any) => async (dispatch: (arg0: { type: string; value: any }) => void) => {
    const contract_address = await address;
    dispatch(get_address(contract_address));
  };

export const get_contract_thunk = (contr: any) => async (dispatch: (arg0: { type: string; value: any }) => void) => {
  const current_contract = contr;
  dispatch(get_contract(current_contract));
};

export const post_contract_thunk = (data: any) => async (dispatch: (arg0: { type: string; value: any }) => void) => {
  api.post_transactions(data).then(
    resp => dispatch(post_contract_data(resp)),
    error => console.log(error),
  );
};

export const get_rate_thunk = () => async (dispatch: (type: any) => void) => {
  api.get_coin_rate().then(
    resp => dispatch(get_rate(resp)),
    error => console.log(error),
  );
};

export const get_health_thunk = () => async (dispatch: (type: any) => void) => {
  api.get_coin_health().then(
    resp => dispatch(get_rate(resp)),
    error => console.log(error),
  );
};

export const get_transactions_thunk = (id: string) => async (dispatch: (type: any) => void) => {
  api.get_coins_transactions(id).then(
    resp => dispatch(get_transactions(resp)),
    error => console.log(error),
  );
};

export const get_specific_transactions_thunk = (id: string) => async (dispatch: (type: any) => void) => {
  api.get_coins_transactions_specific(id).then(
    resp => dispatch(get_transactions_transactions_specific(resp)),
    error => console.log(error),
  );
};

export const put_transactions_thunk = (id: string, hash: string) => async (dispatch: (type: any) => void) => {
  api.put_coins_transactions(id, hash).then(
    resp => dispatch(put_transactions(resp)),
    error => console.log(error),
  );
};
