import { BigNumber } from 'ethers';
import { useEffect, useState } from 'react';

import { bigNum, num } from '../../resources/constants';
import { get_transactions_thunk, put_transactions_thunk } from '../../redux/actions/actions';
import { useDispatch, useSelector } from 'react-redux';
import { RootState } from '../../redux/store';
import { post_transactions } from '../../api/api';

export const usePopUp = (
  data: any,

  setHash: (arg: string) => void,
  closePopUpStateHandler: () => void,
  openPopUpPendingStateHandler: () => void,
  closePopUpPendingStateHandler: () => void,
  closePopUpSpinnerStateHandler: () => void,
  openPopUpSpinnerStateHandler: () => void,
) => {
  const [amount, setAmount] = useState<string>('0');
  const [earned, setEarned] = useState<number>(0);
  const [earnedResult, setEarnedResult] = useState<number>(0);

  const dispatch = useDispatch();
  const onChangeHandler = (e: React.ChangeEvent<HTMLInputElement>) => {
    setAmount(e.target.value);
  };

  const address = useSelector((state: RootState | any) => state.get_contract_address_reducer);

  const earnedCount = +earned;

  useEffect(() => {
    setEarnedResult(earnedCount / bigNum);
  }, [earnedCount]);

  if (typeof data.contract !== 'undefined') {
    data.contract.earned().then(
      (res: any) => setEarned(res.toString()),
      (error: { message: any }) => console.log(error.message),
    );
  }

  if (amount === '' || !amount) {
    setAmount('0');
  }

  const bigAmount = BigNumber.from(amount).mul(num);

  console.log(earnedResult.toFixed(2));

  const onClickHandler = (popUpType: string) => {
    const payload = {
      coin: 'mndcc',
      amount: amount,
      address: address.address,
      type: popUpType === 'buy' ? 'stake' : popUpType === 'remove' ? 'withdraw' : 'claim',
    };

    const payloadEarned = {
      coin: 'mndcc',
      amount: earnedResult.toFixed(2),
      address: address.address,
      type: popUpType === 'buy' ? 'stake' : popUpType === 'remove' ? 'withdraw' : 'claim',
    };

    popUpType === 'buy'
      ? data.contract
          .approve(bigAmount)
          .then((x: any) => {
            setHash(x.hash);
            closePopUpStateHandler();
            openPopUpSpinnerStateHandler();

            post_transactions(payload).then(resp => {
              x.wait(1).then(() => {
                closePopUpSpinnerStateHandler();

                data.contract
                  .stake(bigAmount)
                  .then((y: any) => {
                    setHash(y.hash);
                    openPopUpPendingStateHandler();
                    dispatch(put_transactions_thunk(resp.result.id, y.hash));
                  })
                  .catch((error: any) => {
                    console.log(error.message);
                  })
                  .finally(() => {
                    dispatch(get_transactions_thunk(address.address));
                  });
              });
            });
          })
          .catch((error: any) => {
            console.log(error.message);
          })
      : popUpType === 'remove'
      ? post_transactions(payload).then(resp => {
          data.contract
            .withdraw(bigAmount)
            .then((y: any) => {
              setHash(y.hash);
              openPopUpPendingStateHandler();
              dispatch(put_transactions_thunk(resp.result.id, y.hash));
            })
            .catch((error: any) => {
              console.log(error.message);
            })
            .finally(() => {
              closePopUpStateHandler();
              dispatch(get_transactions_thunk(address.address));
            });
        })
      : post_transactions(payloadEarned).then(resp => {
          data.contract
            .claim()
            .then((y: any) => {
              setHash(y.hash);
              closePopUpStateHandler();
              openPopUpPendingStateHandler();
              dispatch(put_transactions_thunk(resp.result.id, y.hash));
            })
            .catch((error: any) => {
              console.log(error.message);
            })
            .finally(() => {
              closePopUpPendingStateHandler();
              dispatch(get_transactions_thunk(address.address));
            });
        });
  };

  return { onChangeHandler, earnedCount, amount, earned, bigAmount, onClickHandler };
};
