import axios from 'axios';
import { DATA_SERVER_BASE_URL } from '../resources/constants';

export const get_coin_rate = async () => {
  const request = await axios({
    url: `${DATA_SERVER_BASE_URL}/rate`,
    method: 'get',
  });
  const rate = request.data.result;
  return rate;
};

export const get_coin_health = async () => {
  const request = await axios({
    url: `${DATA_SERVER_BASE_URL}/health`,
    method: 'get',
  });
  const health = request.data;
  return health;
};

export const get_coins_transactions = async (id: string) => {
  const request = await axios({
    url: `${DATA_SERVER_BASE_URL}/transaction/fetch/?address=${id}`,
    method: 'get',
  });
  const transactions = request.data;
  return transactions;
};

export const get_coins_transactions_specific = async (id: string) => {
  const request = await axios({
    url: `${DATA_SERVER_BASE_URL}/transaction/${id}`,
    method: 'get',
  });
  const transactions_specific = request.data;
  return transactions_specific;
};

export const put_coins_transactions = async (id: string, hash: string) => {
  const request = await axios({
    url: `${DATA_SERVER_BASE_URL}/transaction/${id}`,
    method: 'put',

    data: { status: 'success', txid: hash },
  });
  const transactions = request.data;
  return transactions;
};

export const post_transactions = async (data: any) => {
  const { txid, coin, amount, address, type } = data;
  const request = await axios({
    url: `${DATA_SERVER_BASE_URL}/transaction`,
    method: 'post',

    data: { txid, coin, amount, address, type },
  });
  const transactions = request.data;
  return transactions;
};
