import { POST_CONTRACT } from '../../resources/constants';

const initial_state = {
  data: {},
};

interface Action {
  value: string;
  type: string;
}

export const post_contract_reducer = (state = initial_state, action: Action) => {
  switch (action.type) {
    case POST_CONTRACT:
      return {
        ...state,
        data: action.value,
      };
    default:
      return state;
  }
};
