import { FC } from 'react';
import styles from './pop_up.module.scss';
import Logo from '../../assets/icons/dashboard_logo.svg';
import CashIcon from '../../assets/icons/streamline-icon-cash-payment-coin-stash@25x25.svg';
import Arrow from '../../assets/icons/icons-tabbar-share.svg';
import cn from 'classnames';
import { BASE_URL } from '../../resources/constants';
import { useTranslation } from 'react-i18next';
import ClipLoader from 'react-spinners/ClipLoader';
import { css } from '@emotion/react';

interface Props {
  isPopUpOpen: boolean;
  data: any;
  popUpType: string;
  closePopUpStateHandler: () => void;
  contractHash: string;
}

const override = css`
  border: 1rem solid #64a640;
  border-bottom-color: white;
  animation: animation-s8tf20 3.5s 0s infinite linear;
`;

export const SpinnedPopUp: FC<Props> = ({ closePopUpStateHandler, popUpType, contractHash }) => {
  const { t } = useTranslation();

  return (
    <section className={styles.wrapper}>
      <div className={styles.pop_up}>
        <div className={styles.upperBlock}>
          <p className={styles.name}>{'Pool 1'}</p>
          <img src={Logo} alt="logo" className={styles.logo} />
          <div className={styles.cross} onClick={closePopUpStateHandler}>
            <div></div>
            <div></div>
          </div>
        </div>

        <div className={styles.dataBlock}>
          <img
            src={CashIcon}
            className={cn(styles.cashIcon, { [styles.cashIconBig]: popUpType === 'claim' })}
            alt="icon"
          />

          <div className={styles.leftPart}>
            <p className={styles.title}>{t('spinned.transaction_pending')}</p>
          </div>
        </div>

        <div className={cn(styles.popUpNameWrapper, styles.popUpNameWrapperPending)} style={{ marginTop: '2rem' }}>
          <p className={styles.popUpName}>{t('spinned.transaction_status')}</p>

          <ClipLoader color={'#64a640'} size={'20rem'} speedMultiplier={0.3} css={override} />

          <p style={{ fontSize: '2.2rem', fontWeight: 200, width: '35rem' }}>{t('spinned.transaction_check')}</p>
          <a href={`${BASE_URL}/${contractHash}`} target="blank" rel="noreferrer">
            <div className={styles.roundedButton}>
              <img src={Arrow} className={styles.arrow} alt="icon" />

              <p>{t('spinned.status_button')}</p>
            </div>
          </a>
        </div>

        <div className={styles.popUpInputWrapper}></div>
      </div>
    </section>
  );
};
