import React, { FC } from 'react';
import styles from '../stake_table/stake_table.module.scss';
import s from './deposits.module.scss';
import cn from 'classnames';

import { useTranslation } from 'react-i18next';
import { timeConverter } from '../../../helpers/helpers';

interface Props {
  ballance: number;
  popUpStateHandler?: (a: string) => void;
  contracts_data: any;
  rate: any;
}

export const Deposits: FC<Props> = ({ ballance, popUpStateHandler, contracts_data, rate }) => {
  const { t } = useTranslation();

  return (
    <section className={styles.wrapper}>
      <div className={styles.dataBlock}>
        <div className={styles.dataWrapper}>
          <p className={styles.title}>{t('staking-page.Einzahlungen.name')}</p>
        </div>
      </div>
      <div className={styles.tableTitles}>
        <div className={styles.tableTitle} style={{ width: '23rem' }}>
          <p style={{ marginLeft: '2rem', marginBottom: '1.5rem' }}>{t('staking-page.stake-table-titles.Pool')}</p>
        </div>
        <div className={styles.tableTitle} style={{ width: '29rem' }}>
          {t('staking-page.Einzahlungen.art')}
        </div>
        <div className={styles.tableTitle} style={{ width: '35rem' }}>
          {t('staking-page.stake-table-titles.MNDCC')}
        </div>
        <div className={styles.tableTitle} style={{ width: '30rem' }}>
          {t('staking-page.stake-table-titles.Wert')} <span style={{ color: '#9A9A9A' }}>USD</span>
        </div>
        <div className={styles.tableTitle} style={{ width: '29rem', marginRight: 0 }}>
          {t('staking-page.Einzahlungen.data')}
        </div>
      </div>
      {ballance !== 0 &&
        typeof contracts_data !== 'undefined' &&
        contracts_data!
          .sort((a: any, b: any) => new Date(b.created_at).getTime() - new Date(a.created_at).getTime())
          .map((row: any, i: number) => (
            <div
              className={cn(
                styles.dataContainer,
                { [s.red]: row.type !== 'stake' },
                { [s.green]: row.type === 'stake' },
              )}
              style={{ marginTop: '1rem' }}
              key={i}
            >
              <div className={styles.tableTitle} style={{ width: '26rem' }}>
                {'mondo staking'}
              </div>
              <div className={styles.tableTitle} style={{ width: '34rem' }}>
                {row.type === 'stake'
                  ? t('table.Anteile')
                  : row.type === 'withdraw'
                  ? t('table.Reward')
                  : t('table.Pulled')}
              </div>
              <div className={styles.tableTitle} style={{ width: '40rem' }}>
                {row.amount}
              </div>
              <div className={styles.tableTitle} style={{ width: '35rem' }}>
                {(row.amount * rate.rate.usd.rate).toFixed(2)}$
              </div>
              <div className={styles.tableTitle} style={{ width: '25rem' }}>
                {timeConverter(row.created_at)}
              </div>
            </div>
          ))}

      {ballance === 0 && (
        <div className={styles.buttonBlock}>
          <div className={styles.button} onClick={() => popUpStateHandler!('buy')}>
            {'buy stake'}
          </div>
        </div>
      )}

      {contracts_data && contracts_data.length === 0 && (
        <div className={styles.buttonBlock}>
          <div className={styles.button} onClick={() => popUpStateHandler!('buy')}>
            {'buy stake'}
          </div>
        </div>
      )}
    </section>
  );
};
