import { GET_ADDRESS } from '../../resources/constants';

const initial_state = {
  address: '',
};

interface Action {
  value: string;
  type: string;
}

export const get_contract_address_reducer = (state = initial_state, action: Action) => {
  switch (action.type) {
    case GET_ADDRESS:
      return {
        ...state,
        address: action.value,
      };
    default:
      return state;
  }
};
