import { PUT_TRANSACTIONS } from '../../resources/constants';

const initial_state = {
  success: '',
};

interface Action {
  value: string;
  type: string;
}

export const put_transactions_reducer = (state = initial_state, action: Action) => {
  switch (action.type) {
    case PUT_TRANSACTIONS:
      return {
        ...state,
        success: action.value,
      };
    default:
      return state;
  }
};
