import { GET_TRANSACTIONS } from '../../resources/constants';

const initial_state = {
  transactions: 0,
};

interface Action {
  value: string;
  type: string;
}

export const get_transactions_reducer = (state = initial_state, action: Action) => {
  switch (action.type) {
    case GET_TRANSACTIONS:
      return {
        ...state,
        transactions: action.value,
      };
    default:
      return state;
  }
};
