import { GET_HEALTH } from '../../resources/constants';

const initial_state = {
  health: 0,
};

interface Action {
  value: string;
  type: string;
}

export const get_health_reducer = (state = initial_state, action: Action) => {
  switch (action.type) {
    case GET_HEALTH:
      return {
        ...state,
        health: action.value,
      };
    default:
      return state;
  }
};
