import { SET_SUPPLY } from '../../resources/constants';

const initial_state = {
  supply: 0,
};

interface Action {
  value: string;
  type: string;
}

export const set_supply_reducer = (state = initial_state, action: Action) => {
  switch (action.type) {
    case SET_SUPPLY:
      return {
        ...state,
        supply: action.value,
      };
    default:
      return state;
  }
};
