import { GET_RATE } from '../../resources/constants';

const initial_state = {
  rate: 0,
};

interface Action {
  value: string;
  type: string;
}

export const get_rate_reducer = (state = initial_state, action: Action) => {
  switch (action.type) {
    case GET_RATE:
      return {
        ...state,
        rate: action.value,
      };
    default:
      return state;
  }
};
