import { GET_TRANSACTIONS_SPECIFIC } from '../../resources/constants';

const initial_state = {
  transactions_specific: 0,
};

interface Action {
  value: string;
  type: string;
}

export const get_specific_transactions_reducer = (state = initial_state, action: Action) => {
  switch (action.type) {
    case GET_TRANSACTIONS_SPECIFIC:
      return {
        ...state,
        transactions_specific: action.value,
      };
    default:
      return state;
  }
};
