import {
  get_contract_address_thunk,
  get_contract_thunk,
  get_rate_thunk,
  get_transactions_thunk,
  set_reward,
  set_supply,
} from '../../../redux/actions/actions';
import { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useDispatch, useSelector } from 'react-redux';
import { RootState } from '../../../redux/store';
import { num } from '../../../resources/constants';
import { contract } from '../../../web3/contract-api';

export const useStakingPage = () => {
  const { t } = useTranslation();
  const [ballance, setBallance] = useState(0);
  const [earned, setEarned] = useState(0);
  const [conn, setConn] = useState(false);
  const [isError, setIsError] = useState('');
  const [isPopUpOpen, setPopUpState] = useState<boolean>(false);
  const [popUpType, setPopUpType] = useState('');
  const [isPendingPopUpOpen, setIsPendingPopUpOpen] = useState(false);
  const [isSpinnerPopUpOpen, setIsSpinnerPopUpOpen] = useState(false);
  const [contractHash, setContractHash] = useState('');
  const [supply, setSupply] = useState(0);
  const [rewardPerToken, setRewardPerToken] = useState(0);

  const dispatch = useDispatch();

  const address = useSelector((state: RootState | any) => state.get_contract_address_reducer);
  const contracts_data = useSelector((state: RootState | any) => state.get_transactions_reducer.transactions.result);
  const rate = useSelector((state: RootState | any) => state.get_rate_reducer);

  const popUpStateHandler = (type: string) => {
    setPopUpState(!isPopUpOpen);
    setPopUpType(type);
  };

  const closePopUpStateHandler = () => {
    setPopUpState(false);
  };

  const closePopUpPendingStateHandler = () => {
    setTimeout(() => {
      setIsPendingPopUpOpen(false);
    }, 4000);
  };

  const openPopUpPendingStateHandler = () => {
    setIsPendingPopUpOpen(true);
  };

  const closePopUpSpinnerStateHandler = () => {
    setIsSpinnerPopUpOpen(false);
  };

  const openPopUpSpinnerStateHandler = () => {
    setIsSpinnerPopUpOpen(true);
  };

  const setHash = (hash: string) => {
    setContractHash(hash);
  };

  useEffect(() => {
    dispatch(get_rate_thunk());
  }, [dispatch]);

  useEffect(() => {
    const intervalContract = setInterval(() => {
      !conn &&
        contract.connect().then(
          res => {
            setConn(true);
            dispatch(get_contract_address_thunk(res.contract.getAddress()));
            dispatch(get_contract_thunk(res));
            setIsError('');
          },

          error => setIsError(error.message),
        );
    }, 2000);
    return () => clearInterval(intervalContract);
  });

  useEffect(() => {
    //  dispatch(get_specific_transactions_thunk(address.address));
    dispatch(get_transactions_thunk(address.address));
  }, [address, dispatch]);

  useEffect(() => {
    closePopUpPendingStateHandler();
  }, [ballance]);

  useEffect(() => {
    if (typeof data.contract === 'undefined') {
      return;
    }
    const intervalContract = setInterval(() => {
      data.contract.balanceOf().then(
        (res: any) => setBallance(res.div(num).toString()),
        (error: { message: any }) => console.log(error.message),
      );

      data.contract.earned().then(
        (res: any) => setEarned(res.div(num).toString()),
        (error: { message: any }) => console.log(error.message),
      );
    }, 2000);
    return () => clearInterval(intervalContract);
  });
  const data = useSelector((state: RootState | any) => state.get_contract_reducer.current_contract);
  const message = 'Already processing eth_requestAccounts. Please wait.';
  const earnedCount = +earned;
  const ballanceCount = +ballance;

  if (typeof data.contract !== 'undefined') {
    data.contract.totalSupply().then(
      (res: any) => {
        dispatch(set_supply(res.div(num).toString()));
        setSupply(res.div(num).toString());
      },
      (error: { message: any }) => error.message,
    );

    data.contract.rewardPerToken().then(
      (res: any) => {
        dispatch(set_reward(res.div(num).toString()));
        setRewardPerToken(res.div(num).toString());
      },
      (error: { message: any }) => error.message,
    );
  }

  const rewardNum = +rewardPerToken;
  const supplyNum = +supply;
  const availableRewards = 100000 - supplyNum * rewardNum;
  const antail = (ballanceCount / supplyNum) * 100;

  const earings = (ballanceCount / rewardNum) * 100;

  return {
    data,
    message,
    isError,
    popUpType,
    isPopUpOpen,
    earnedCount,
    ballanceCount,
    availableRewards,
    isPendingPopUpOpen,
    isSpinnerPopUpOpen,
    contractHash,
    antail,
    earings,
    contracts_data,
    rate,

    setHash,
    closePopUpPendingStateHandler,
    openPopUpPendingStateHandler,
    closePopUpSpinnerStateHandler,
    openPopUpSpinnerStateHandler,
    t,
    closePopUpStateHandler,
    popUpStateHandler,
  };
};
