import { get_contract_thunk } from './../../../redux/actions/actions';
import { useEffect, useState } from 'react';
import { buildStyles } from 'react-circular-progressbar';
import { useTranslation } from 'react-i18next';
import { useDispatch, useSelector } from 'react-redux';
import { contract } from '../../../web3/contract-api';
import { RootState } from '../../../redux/store';

export const useDashboard = () => {
  const [isError, setIsError] = useState('');
  const [conn, setConn] = useState(false);
  const { t } = useTranslation();
  const dispatch = useDispatch();
  const data = useSelector((state: RootState | any) => state.get_contract_reducer);
  const supplyRedux = useSelector((state: RootState | any) => state.supply);
  const rewardRedux = useSelector((state: RootState | any) => state.reward);

  const build_styles = buildStyles({
    // strokeLinecap: 'butt',
    textColor: '#64a640',
    pathColor: '#64a640',
    trailColor: '#c6b5a4',
  });
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  useEffect(() => {
    const intervalContract = setInterval(() => {
      !conn &&
        contract.connect().then(
          res => {
            setConn(true);
            dispatch(get_contract_thunk(res));
            setIsError('');
          },

          error => setIsError(error.message),
        );
    }, 2000);
    return () => clearInterval(intervalContract);
  });

  const rewardNum = +rewardRedux.reward_per_token;

  const supplyNum = supplyRedux ? +supplyRedux.supply : 0;
  const availableRewards = 100000 - supplyNum * rewardNum;
  const antail = supplyNum / rewardNum;

  //let percentage = rewardNum >= 0 && supplyNum >= 0 ? (100000 - supplyNum * rewardNum) / 100000 : 0;
  let percentage = isNaN(rewardNum / supplyNum) ? 0 : rewardNum / (supplyNum / 100);

  return { build_styles, percentage, data, supplyNum, isError, conn, availableRewards, antail, t };
};
