import { createStore, combineReducers, applyMiddleware } from 'redux';
import thunk from 'redux-thunk';

import { composeWithDevTools } from 'redux-devtools-extension';
import {
  get_contract_reducer,
  get_health_reducer,
  get_rate_reducer,
  set_language_reducer,
  get_transactions_reducer,
  put_transactions_reducer,
  get_specific_transactions_reducer,
  get_contract_address_reducer,
  post_contract_reducer,
  set_supply_reducer,
  set_reward_reducer,
} from './reducers';

const rootReducer = combineReducers({
  set_language_reducer,
  get_contract_reducer,
  get_rate_reducer,
  get_health_reducer,
  get_transactions_reducer,
  put_transactions_reducer,
  get_specific_transactions_reducer,
  get_contract_address_reducer,
  post_contract_reducer,
  supply: set_supply_reducer,
  reward: set_reward_reducer,
});
export type RootState = ReturnType<typeof rootReducer>;

const store = createStore(rootReducer, composeWithDevTools(applyMiddleware(thunk)));

export default store;
